
.detail-header{
    margin-top: 20px;
    margin-bottom: 20px;
}

.detail-alert{
    margin: 0 auto;
    text-align: center;
    max-width: 400px;
    background-color: #c44569;
    margin-bottom: 20px;
    font-size: 18px;
    font-weight: 700;
    height: 50px;
    line-height: 50px;
    border-radius: 5px;
    color: #fff;
    animation-name: alertColor;
    animation-duration: 1.3s;
    animation-iteration-count: infinite;
    
}

.cart-alert{
    p{
            
    
    }
    em{
      font-style: normal;
      font-size: 9px;
      display: inline-block;
      
      
    }
    line-height: 30px;
    animation: none;
    height: auto;
    cursor: pointer;
    margin-top: 20px;

  @extend .detail-alert
}

@keyframes alertColor{
 
    to{
        opacity: 0;
    }
}

.tabs-enter{
    opacity: 0;

}

.tabs-enter-active{
    opacity: 1;
    transition: all 500ms;
}